@keyframes rotate-hand {
  from {
    transform-origin: bottom;
    transform: rotate(0);
  }

  to {
    transform-origin: bottom;
    transform: rotate(360deg);
  }
}

.page__body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__central-circle {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__minutes-hand {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-hand;
  position: absolute;
  bottom: 40vmin;
}

.stopwatch__seconds-hand {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-hand;
  position: absolute;
  bottom: 40vmin;
}

.stopwatch--speed-up .stopwatch__minutes-hand {
  animation: 600s steps(60, end) infinite rotate-hand;
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  animation: 10s linear infinite rotate-hand;
}
